import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainPageComponent } from './main-page/main-page.component';
import { NgCntBankHistoryPageComponent } from './ng-cnt-bank-history-page/ng-cnt-bank-history-page.component';
import { NgCntBankPaymentPageWrapperComponent } from './ng-cnt-bank-payment-page-wrapper/ng-cnt-bank-payment-page-wrapper.component';
import { NgCntBankSearchHistoryPageComponent } from './ng-cnt-bank-search-history-page/ng-cnt-bank-search-history-page.component';
import { NgCntBankProductListPageComponent } from './ng-cnt-bank-product-list-page/ng-cnt-bank-product-list-page.component';
import { NgCntBankProductSinglePageComponent } from './ng-cnt-bank-product-single-page/ng-cnt-bank-product-single-page.component';
import { NgBankCardsPageComponent } from './ng-bank-cards-page/ng-bank-cards-page.component'
import { NgSntBankSliderCardsPageComponent } from './ng-snt-bank-slider-cards-page/ng-snt-bank-slider-cards-page.component';
import { NgCntBankScanCodePageWrapperComponent } from './ng-cnt-bank-scan-code-page-wrapper/ng-cnt-bank-scan-code-page-wrapper.component';

const routes: Routes = [
  {
    path: '',
    component: MainPageComponent,
  },
  {
    path: 'main',
    component: MainPageComponent,
  },
  {
    path: 'history',
    component: NgCntBankHistoryPageComponent,
  },
  {
    path: 'payments',
    component: NgCntBankPaymentPageWrapperComponent,
  },
  {
    path: 'search',
    component: NgCntBankSearchHistoryPageComponent,
  },
  {
    path: 'catalog',
    component: NgCntBankProductListPageComponent,
  },
  {
    path: 'single-page',
    component: NgCntBankProductSinglePageComponent,
  },
  {
    path: 'my-cards',
    component: NgBankCardsPageComponent,
  },
  {
    path: 'cards',
    component: NgSntBankSliderCardsPageComponent,
  },
  {
    path: 'scan',
    component: NgCntBankScanCodePageWrapperComponent,
  },
  {
    path: '**',
    component: MainPageComponent,
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
