import { Component } from '@angular/core';
import { BankService } from './bank.service';

@Component({
  selector: 'cnt-nx-workspace-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  constructor(private StorageBank: BankService) {}

  title = 'main-cnt-bank';

  public getBankFooterMock() {
    return this.StorageBank.getBankFooterMock();
  }

  public clickOnFooterItem(id){
    this.StorageBank.navigateFooterItem(id.detail)
  }
}
